.ant-modal-content {
  box-sizing: border-box;
  vertical-align: top;
  background: linear-gradient(315deg, #2d3436, #000 74%);
  border: 2px solid #2a2b2f;
  border-radius: 20px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
  color: rgba($color: #ffffff, $alpha: 0.85);

  .ant-modal-header {
    background-color: transparent;
    border-bottom: 1px solid rgba($color: #ffffff, $alpha: 0.15);

    .ant-modal-title {
      color: rgba($color: #ffffff, $alpha: 0.85);
    }
  }

  .ant-modal-close-x {
    svg {
      fill: rgba($color: #ffffff, $alpha: 0.85);
    }
  }

  .ant-btn {
    background-color: #4b83eb;
    text-align: center;
    padding: 16px 20px;
    border-radius: 7px;
    font-weight: bold;
    transition: all 0.2s ease-out;
    height: auto;

    &:hover {
      background-color: #ace65c;
      color: black;
      border-color: #ace65c;
    }

    &:disabled {
      background-color: #727272;
      color: white;
      border-color: #727272;
    }
  }

  .ant-input {
    height: auto;
    padding: 16px 20px;
    font-weight: bold;
    border-radius: 7px;
    background-color: rgba($color: #ffffff, $alpha: 0.85);
  }

  .ant-form-item-label {
    label {
      color: rgba($color: #ffffff, $alpha: 0.85);
    }
  }
}

.token-info {
  margin-bottom: 16px;
  padding: 8px 0;
  display: flex;
  color: rgba($color: #ffffff, $alpha: 0.85);
  border-bottom: 1px solid rgba($color: #ffffff, $alpha: 0.15);
  font-size: 12px;
  .title {
    flex: 1;
  }
  .value {
    font-weight: 600;
    color: white;
    font-size: 12px;
  }
}
