.pools_detail {
  width: 100%;
}

.pools_detail {
  margin-top: 60px;
  padding-bottom: 176px;
  &_btn_box {
    margin: 0 auto;
    padding-bottom: 60px;
    text-align: center;
  }
  &_btn {
    display: inline-block;
    width: 128px;
    height: 48px;
    font-size: 16px;
    color: #22292f;
    font-weight: 600;
    line-height: 48px;
    border-radius: 7px;
    background: #f2f0eb;
    &:hover {
      color: #fff;
    }
    &:last-child {
      margin-left: 24px;
      &:hover {
        color: #4b83eb;
      }
    }
  }
  &_btn_active {
    color: #fff;
    background: #4b83eb;
    &:hover {
      color: #fff;
      background: #4b83eb;
    }
  }

  &_btn_disable {
    color: #fff;
    background: #999;
    box-shadow: 0px 4px 20px 0px rgba(#999, 0.3);
  }
  &_record {
    position: relative;
    box-sizing: border-box;
    padding: 25px;
    vertical-align: top;
    background: linear-gradient(315deg,#2d3436,#000 74%);
    border: 2px solid #2a2b2f;
    border-radius: 20px;
    box-shadow: 0 20px 50px rgba(0,0,0,.5);
    transition: .5s;
    margin: 40px 80px 80px 80px;
    @media (max-width: 768px) {
      margin: 18px;
      padding: 12px;
      margin-top: 0;
    }
    .mask_layer {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 1;
      padding: 0 30px;
      font-size: 14px;
      font-weight: 500;
      color: #ffffff;
      line-height: 164px;
      text-align: center;
      background: rgba(34, 41, 47, 0.7);
      border-radius: 10px;
    }
  }
  &_record_tab {
    a {
      position: relative;
      margin: 0 36px 22px 0;
      height: 60px;
      display: inline-block;
      font-size: 16px;
      font-weight: 500;
      color: #7a7f82;
      line-height: 60px;
      &:hover {
        color: #ace65c;
        &:after {
          position: absolute;
          content: "";
          bottom: 0;
          left: 0;
          width: 100%;
          height: 4px;
          background: #ace65c;
        }
      }
    }
    a.active {
      color: #ace65c;
      &:after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 0;
        width: 100%;
        height: 4px;
        background: #ace65c;
      }
    }
  }
  &_record_title {
    width: 100%;
    thead {
      tr {
        td {
          font-size: 12px;
          font-weight: 400;
          color: rgba(255, 255, 255, 0.5);
          line-height: 28px;
          @media (max-width: 768px) {
            font-size: 10px;
            line-height: 13px;
          }
          &:last-child {
            text-align: right;
          }
        }
      }
    }
    tbody {
      tr {
        td {
          font-size: 14px;
          font-weight: 500;
          color: rgba(255, 255, 255, 0.5);
          line-height: 16px;
          @media (max-width: 768px) {
            font-size: 12px;
            padding: 0 4px;
          }
          &:last-child {
            text-align: right;
          }
        }
        .pools_detail_record_btn {
          display: inline-block;
          padding: 0 8px;
          font-size: 14px;
          color: black;
          line-height: 24px;
          background: #ace65c;
          border-radius: 12px;
          text-align: right;

          @media (max-width: 768px) {
            background-color: transparent;
            color: #ace65c;
            font-size: 12px;
            padding: 0 4px;
            text-align: center;
          }
        }
      }
    }
  }
}


.pools-container {
  display: flex;
  align-items: stretch;
  padding: 0 80px 20px 80px;

  @media (max-width: 768px) {
    padding: 18px;
    flex-direction: column;
  }

  .pools-information {
    margin-right: 25px;

    @media (max-width: 768px) {
      margin-right: 0;
      margin-bottom: 18px;
      flex: 1;
    }
  }

  .pools-information, .pools-joined {
    flex: 1;
    box-sizing: border-box;
    padding: 25px;
    vertical-align: top;
    background: linear-gradient(315deg,#2d3436,#000 74%);
    border: 2px solid #2a2b2f;
    border-radius: 20px;
    box-shadow: 0 20px 50px rgba(0,0,0,.5);
    transition: .5s;

    @media (max-width: 768px) {
      padding: 12px;
    }
  }

  .private, .address {
    font-family: Poppins-Bold,sans-serif;
    background-color: #4b83eb;
    background-image: linear-gradient(315deg,#ace65c,#4b83eb 74%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: drop-shadow(0 3px 6px rgba(170,169,173,.9));
  }

  .pools-item-container {
    padding: 14px 0;
    display: flex;
    align-items: center;
  }

  .pools-item-label {
    font-size: 14px;
    font-weight: 300;
    z-index: 1;
    transition: .5s;
    color: hsla(0,0%,100%,.5);
    flex: 1;
  }
  .pools-item-value {
    color: #fdfdfd;
    font-size: 14px;
    font-weight: normal !important;
    word-break: break-all;
  }

  .pools-sharing {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 18px;
    svg {
      --color: #fdfdfde7;
      &:hover {
        color: white;
      }
    }
    a {
      color: #fdfdfde7;
      &:hover {
        color: white;
      }
      margin-right: 8px;
    }
  }

  .pools-buttons {
    padding-top: 20px;
  }

  .comming {
    color: #ace65c;
    text-align: center;
  }
  
  .btn-wrapper {
    display: flex;
    align-items: center;
  }
  .pools-btn {
    display: block;
    cursor: pointer;
    width: 100%;
    text-align: center;
    padding: 16px 20px;
    border-radius: 7px;
    font-weight: bold;
    transition: all 0.2s ease-out;
    flex: 1;

    &.active {
      background-color: #4b83eb;
      color: white;

      &:hover {
        background-color: #ace65c;
        color: black;
      }
    }

    &.disabled {
      background-color: #a7a7a7;
      color: white;
      pointer-events: none;
    }

    &.heco {
      background-color: #e9e9e9;
      color: rgb(10, 10, 10);
      margin-left: 20px;
    }
  }
}


// VARS
$circle-width: 0.09em;
$circle-width-hover: 0.07em;

// colors default
$primary-color: #000000; // czarny
$secondary-color: #dfe8ed; //szary bcg
$bg-color: #ffffff; //srodkowy bezowy 

// colors customized
$primary-color-blue: #4b83ebcc;
$primary-color-span: #3c4761;

.pie {
	position: absolute;
	border: $circle-width solid $primary-color;
	width: 1 - (2 * $circle-width);
	height: 1 - (2 * $circle-width);
	clip: rect(0em, 0.5em, 1em, 0em);
	border-radius: 50%;
	transform: rotate(0deg);
}

.pie-fill {
	transform: rotate(180deg);
}

.rect-auto{
	clip: rect(auto, auto, auto, auto);
}

.c100 {
	*, *:before, *:after {
		box-sizing: content-box;
	}

	position: relative;
	font-size: 120px;
	width: 1em;
	height: 1em;
	border-radius: 50%;
	margin: 0 auto;
	background-color: $secondary-color;
	> span {
		position: absolute;
		width: 100%;
		z-index: 1;
		left: 0;
		top: 0;
		width: 5em;
		line-height: 5em;
		font-size: 0.2em;
		color: $primary-color-span;
		display: block;
		text-align: center;
		white-space: nowrap;
    transition: all 0.2s ease-out;
	}

	// background inside the circle
	&:after{
		position: absolute;
		top: $circle-width;
		left: $circle-width;
		display: block;
		content: " ";
		border-radius: 50%;
		background-color: $bg-color;
		width: 1 - (2 * $circle-width);
		height: 1 - (2 * $circle-width);
		transition: all 0.2s ease-in;

	}

	// the slice (mask)
	.slice {
		position: absolute;
		width: 1em;
		height: 1em;
		clip: rect(0em, 1em, 1em, 0.5em);
	}

	// circle to show the status
	.bar {
		@extend .pie;
	}


	// loop to create all needed elements automatically
	@for $j from 51 through 100 {

		&.p#{$j} .slice {
			@extend .rect-auto;
		}

		&.p#{$j} .bar:after{
			@extend .pie-fill;
		}

		&.p#{$j} .fill{
			@extend .pie;
			@extend .pie-fill;
		}

	}

	// loop to rotate all 100 circles
	@for $j from 1 through 100 {
		&.p#{$j} .bar {
			transform: rotate((360/100*$j) + deg);
		}
	}
  
	// blue
	&.blue{

		.bar, .fill { border-color: $primary-color-blue !important;}

		&:hover{
			> span { color: $primary-color-span;}
		}

	}

}
