.pools_header {
  position: relative;
  canvas {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1;
  }
  .ht-balance {
    display: flex;
    align-items: center;
    padding: 0 12px;
    height: 38px;
    border-radius: 5px 0px 0px 5px;
    color: white;
    border: 1px solid white;
    border-right: none;
    p {
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 600;
    }

    @media (max-width: 768px) {
      display: none;
    }
  }
  &.menu-show {
    border-bottom: transparent;

    .pools_header__menu {
      display: block;
    }
  }

  &-account {
    display: flex;
    justify-content: inherit;
    align-items: center;
    position: relative;
    color: #fff;
    transition: 1s;
    height: 38px;
    padding: 0 16px;
    border-radius: 5px;
    border: 1px solid white;

    @media #{$desktop} {
      border-radius: 0 5px 5px 0;
    }

    @media #{$mdWidth} {
      width: 153px;
      position: absolute;
      top: 21px;
      right: 65px;
    }

    @media (max-width: 470px) {
      right: unset;
      // left: 20px;
    }

    @media (max-width: 370px) {
      width: 143px;
    }

    .balance {
      width: 160px;
      text-align: right;

      p {
        color: #fff;
        margin-right: 16px;

        @media (max-width: 370px) {
          margin-right: 8px;
        }
      }
    }

    .address {
      max-width: 153px;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      @media (max-width: 370px) {
        max-width: 120px;
      }

      img {
        width: 24px;
        margin-left: 4px;
      }
    }

    .point {
      width: 16px;
      height: 16px;
      background: var(--yellow);
      border-radius: 50%;
      margin-left: 16px;

      @media (max-width: 370px) {
        display: none;
      }
    }
  }

  &__box {
    display: flex;
    align-items: center;
    height: 100px;
    min-height: 64px;

    @media #{$mdWidth} {
      display: flex;
      height: auto;
      padding: 20px 0;
    }
    @media #{$smWidth} {
      display: flex;
      height: auto;
      padding: 20px 0;
    }
    @media #{$xsWidth} {
      display: flex;
      height: auto;
      padding: 20px 0;
    }
    .more {
      margin-left: 7px;
      display: none;
    }
    @media #{$mdWidth} {
      .more {
        display: block;
      }
    }
    @media #{$smWidth} {
      .more {
        display: block;
      }
    }
    @media #{$xsWidth} {
      .more {
        display: block;
      }
    }
  }

  &__logo {
    position: relative;

    svg {
      width: 155px;
      stroke: var(--color);

      @media #{$mdWidth} {
        width: 155px;
      }
    }

    @media (max-width: 580px) {
      display: none;
    }

    &--small {
      display: none;
      @media #{$desktop} {
        svg {
          width: 155px;
          height: 24px;
        }
      }
      @media #{$mdWidth} {
        svg {
          width: 155px;
          height: 24px;
        }
      }
      @media #{$smWidth} {
        svg {
          width: 100px;
          height: 16px;
        }
      }
      @media #{$xsWidth} {
        svg {
          width: 100px;
          height: 16px;
        }
      }

      &.active {
        @media (max-width: 470px) {
          display: block;
        }
      }

      @media (max-width: 580px) {
        display: block;
      }
    }
  }

  &__menu {
    display: flex;
    align-items: center;
    color: #fdfdfd;
    flex: 1;
    justify-content: flex-end;
    font-size: 14px;
    font-weight: 500;
    margin-left: 25px;
    text-decoration: none;
    transition: all .3s ease;
    filter: drop-shadow(0 6px 3px rgba(170,169,173,.5));

    &-wrapper {
      display: flex;
      align-items: center;
      margin-left: auto;
      margin-right: 0;
    }
  }

  &__btn {
    // margin-left: 24px;
    @media #{$mdWidth} {
      display: block;
      position: absolute;
      top: 9px;
      right: 70px;
    }

    @media (max-width: 370px) {
      position: static;
    }
    @media #{$smWidth} {
      position: static;
    }
    @media #{$xsWidth} {
      position: static;
    }
    @media #{$mdWidth} {
      position: static;
    }
    .connect-btn {
      min-width: 106px;
      height: 38px;
      background: transparent;
      border-radius: 5px;
      outline: none;
      border: none;
      font-size: 14px;
      font-family: -apple-system, BlinkMacSystemFont, Roboto, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-weight: 600;
      color: white;
      cursor: pointer;
      display: flex;
      align-items: center;
      padding: 0 16px;
      border: 1px solid white;

      &:hover {
        background-color: white;
        color: #4b83eb;
        border-color: white;
      }

      img {
        width: 16px;
        height: 16px;
        margin-left: 2px;
      }
    }

    .buttonContainer {
      width: 260px;
      display: flex;
      justify-content: flex-end;
      align-items: center;

      @media #{$mdWidth} {
        width: auto;
      }
    }
  }

  &__lk {
    line-height: 38px;
    white-space: nowrap;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 20px;
    display: flex;

    &-balance {
      padding: 0 12px;
      color: var(--color);
    }

    &-number {
      padding: 0 50px 0 18px;
      position: relative;
      background-color: #fff;
      color: var(--bg);
      border-radius: 20px;
      margin-left: 18px;
      font-size: 14px;

      &::after {
        position: absolute;
        width: 16px;
        height: 16px;
        top: 0;
        right: 16px;
        bottom: 0;
        margin: auto;
        content: '';
        border-radius: 50%;
        background-color: var(--yellow);
      }
    }
  }
}

@media #{$mdWidth} {
  .btn-menu-toggle {
    width: 48px;
    height: 48px;
    position: absolute;
    top: 10px;
    right: 8px;

    &::after,
    &::before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      width: 24px;
      height: 2px;
      transition: var(--transitionDefault);
      background-color: var(--color);
      content: '';
    }

    &::before {
      transform: rotate(180deg) translate3d(0, 5px, 0);
    }

    &::after {
      transform: rotate(180deg) translate3d(0, -5px, 0);
    }

    .menu-show & {
      &::before {
        transform: rotate(45deg) translate3d(0, 0px, 0);
      }

      &::after {
        transform: rotate(-45deg) translate3d(0, 0px, 0);
      }
    }
  }

  .menu-show {
    .main {
      display: none;
    }

    .pools_header {
      flex-grow: 1;
      border-bottom: 0;
    }

    .pools_header__menu {
      display: block !important;
    }

    .footer {
      background-color: transparent;
    }
  }
}
@media #{$smWidth} {
  .btn-menu-toggle {
    display: none;
  }
}
@media #{$xsWidth} {
  .btn-menu-toggle {
    display: none;
  }
}
