.pools-type {
  width: 100%;
  p {
    margin-bottom: 0;
  }
}
.pools-type_card_box_border {
  box-sizing: border-box;
}
.pool-sharing {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 18px;
  svg {
    --color: #fdfdfde7;
    &:hover {
      color: white;
    }
  }
  a {
    color: #fdfdfde7;
    &:hover {
      color: white;
    }
    margin-right: 8px;
  }
}
.pool-contents {
  display: flex;
  align-items: center;
}
.pool-flex {
  flex: 1;
}
.pools-type_card {
  display: flex;
  align-items: stretch;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;

  @media all and (min-width: 767px) {
    padding: 0 60px;
  }

  .box {
    width: 100%;

    @media all and (min-width: 767px) {
      width: 33.33333%;
    }
  }
}
.pools-type {
  &-top {
    min-height: 600px;
    background: transparent;
  }
  &_content {
    margin: 0 auto;
    padding-bottom: 50px;
  }
  &_tab {
    padding-top: 60px;
    padding: 60px 80px 0 60px;
    display: flex;
    justify-content: flex-end;
    text-align: right;
    h2 {
      position: relative;
      margin-bottom: 0;
      margin-left: 20px;
      font-size: 14px;
      color: rgba($color: #fff, $alpha: 0.9);
      &:hover {
        cursor: pointer;
        color: #4b83eb;
      }
      &.tab_active {
        color: #4b83eb;
        background-color: #4b83eb;
        background-image: linear-gradient(315deg, #ace65c, #4b83eb 74%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        filter: drop-shadow(0 3px 6px rgba(170, 169, 173, 0.9));
      }
    }
  }
  &_noData {
    padding-top: 120px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    img {
      width: 370px;
      height: 200px;
    }
    p {
      margin-top: 16px;
      font-size: 12px;
      font-weight: 400;
      color: white;
    }
  }
  .pool-label {
    font-size: 14px;
    font-weight: 300;
    z-index: 1;
    transition: .5s;
    color: hsla(0,0%,100%,.5);
    text-align: left !important;
    padding-bottom: 12px;
    display: flex;

    span {
      flex: 1;
    }
  }
  .pool-value {
    color: #fdfdfd;
    text-align: right;
  }
  .pool-access-text {
    color: #4b83ebcc;
  }
  .pool_status {
    color: #4b83ebcc;
  }
  &_card {
    text-align: center;
    .pools-type_card_box {
      box-sizing: border-box;
      padding: 25px;
      min-height: 368px;
      height: 100%;
      vertical-align: top;
      background: linear-gradient(315deg,#2d3436,#000 74%);
      border: 2px solid #2a2b2f;
      border-radius: 20px;
      box-shadow: 0 20px 50px rgba(0,0,0,.5);
      transition: .5s;
    }
    .pools-type_hover_style {
      &:hover {
        cursor: pointer;
        border: 1px solid #4b83eb;
      }
    }
  }
  &_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    img {
      display: inline-block;
      width: 50px;
      height: 50px;
      margin-right: 6px;
    }
    .pools-type_card_title {
      font-family: Poppins-Bold,sans-serif;
      background-color: #4b83eb;
      background-image: linear-gradient(315deg, #ace65c, #4b83eb 74%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      filter: drop-shadow(0 3px 6px rgba(170,169,173,.9));
      font-weight: bold;
      text-transform: uppercase;
    }
    .pools-type_card_title_right {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
    .pools-type_time {
      display: flex;
      align-items: center;
      margin-bottom: 0;
      span {
        font-style: normal;
        display: inline-block;
        padding: 0 4px;
        height: 28px;
        text-align: center;
        background: black;
        border: 1px solid #ace65c;
        border-radius: 3px;
        font-size: 14px;
        color: #ace65c;
        line-height: 28px;
      }
    }
    span {
      display: inline-block;
      padding: 0 4px;
      height: 18px;
      font-size: 12px;
      font-weight: 400;
      color: #22292f;
      line-height: 18px;
      transform: scale(0.9);
      border-radius: 3px;
    }
    .pools-type_card_access {
      margin-top: 20px;
      line-height: 24px;
      display: flex;
      align-items: center;
    }
  }
  &_enter {
    margin-top: 24px;
    display: inline-block;
    text-decoration: underline;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    font-weight: 500;
    color: #ffffff;
    background: transparent;
    border-radius: 7px;
    text-align: center;
    text-transform: uppercase;
    &:hover {
      color: #4b83eb;
      text-decoration: underline;
    }
  }
  &_disable_enter {
    color: #fff;
    text-decoration: line-through;

    &:hover {
      text-decoration: line-through;
    }
  }
  &_lbp_enter {
    margin-top: 0px;
  }
  .w_bg {
    display: inline-block;
    margin-top: 0;
    width: 182px;
    height: 100px;
  }
}

// VARS
$circle-width: 0.09em;
$circle-width-hover: 0.07em;

// colors default
$primary-color: #000000; // czarny
$secondary-color: #dfe8ed; //szary bcg
$bg-color: #ffffff; //srodkowy bezowy 

// colors customized
$primary-color-blue: #4b83ebcc;
$primary-color-span: #3c4761;

.pie {
	position: absolute;
	border: $circle-width solid $primary-color;
	width: 1 - (2 * $circle-width);
	height: 1 - (2 * $circle-width);
	clip: rect(0em, 0.5em, 1em, 0em);
	border-radius: 50%;
	transform: rotate(0deg);
}

.pie-fill {
	transform: rotate(180deg);
}

.rect-auto{
	clip: rect(auto, auto, auto, auto);
}

.c100 {
	*, *:before, *:after {
		box-sizing: content-box;
	}

	position: relative;
	font-size: 120px;
	width: 1em;
	height: 1em;
	border-radius: 50%;
	margin: 0 auto;
	background-color: $secondary-color;
	> span {
		position: absolute;
		width: 100%;
		z-index: 1;
		left: 0;
		top: 0;
		width: 5em;
		line-height: 5em;
		font-size: 0.2em;
		color: $primary-color-span;
		display: block;
		text-align: center;
		white-space: nowrap;
    transition: all 0.2s ease-out;
	}

	// background inside the circle
	&:after{
		position: absolute;
		top: $circle-width;
		left: $circle-width;
		display: block;
		content: " ";
		border-radius: 50%;
		background-color: $bg-color;
		width: 1 - (2 * $circle-width);
		height: 1 - (2 * $circle-width);
		transition: all 0.2s ease-in;

	}

	// the slice (mask)
	.slice {
		position: absolute;
		width: 1em;
		height: 1em;
		clip: rect(0em, 1em, 1em, 0.5em);
	}

	// circle to show the status
	.bar {
		@extend .pie;
	}


	// loop to create all needed elements automatically
	@for $j from 51 through 100 {

		&.p#{$j} .slice {
			@extend .rect-auto;
		}

		&.p#{$j} .bar:after{
			@extend .pie-fill;
		}

		&.p#{$j} .fill{
			@extend .pie;
			@extend .pie-fill;
		}

	}

	// loop to rotate all 100 circles
	@for $j from 1 through 100 {
		&.p#{$j} .bar {
			transform: rotate((360/100*$j) + deg);
		}
	}
  
	// blue
	&.blue{

		.bar, .fill { border-color: $primary-color-blue !important;}

		&:hover{
			> span { color: $primary-color-span;}
		}

	}

}
