.menumask {
  position: relative;
  width: 100%;
  height: 100vh;
  position: fixed;
  background: rgba(34, 41, 47, 0.6);
  left: 0;
  top: 0;
  touch-action: none;
  z-index: 99;
  &_left {
    height: 100%;
  }
  &_right {
    top: 0;
    width: 250px;
    height: 100%;
    background: white;
    position: absolute;
    animation: mask_right 0.25s;
    right: 0;
  }
  &_guide {
    font-size: 14px;
    font-weight: 600;
    color: #22292f;
    &:hover {
      color: #4b83eb;
    }
  }
  &_ht-balance {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 12px;
    height: 38px;
    background: white;
    border: 1px solid #4b83eb;
    border-radius: 4px;
    width: 100%;
    p {
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 600;
      color: #22292f;
    }
  }
  &_language {
    position: absolute;
    bottom: 18px;
    left: 28px;
  }
  &_header-account {
    display: flex;
    justify-content: inherit;
    align-items: center;
    position: relative;
    color: #000;
    transition: 1s;
    height: 38px;
    padding: 0 16px;
    border-radius: 5px;

    .balance {
      width: 160px;
      text-align: right;
      color: white;

      p {
        color: #fff;
        margin-right: 16px;
      }
    }

    .address {
      max-width: 153px;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      img {
        width: 24px;
        margin-left: 4px;
      }
    }

    .point {
      width: 16px;
      height: 16px;
      background: var(--yellow);
      border-radius: 50%;
      margin-left: 16px;
    }
  }
  @keyframes mask_right {
    0% {
      right: -80%;
    }
    100% {
      right: 0%;
    }
  }
  &_nav {
    padding: 60px 40px 40px;
    width: 100%;
    height: 100%;
  }
  &_list {
    padding-bottom: 25px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    border-bottom: 1px solid #ebe9e3;

    & > div:first-child {
      width: 100%;
    }
  }
  &_item {
    margin: 15px 0;
  }
  &_link {
    position: relative;
    font-size: 14px;
    font-weight: 600;
    line-height: 24px;
    &:hover {
      color: #888;
    }
  }
  .active {
    color: #4b83eb;
  }
}
@media #{$desktop} {
  .menumask {
    display: none;
  }
}
@media #{$mdWidth} {
  .menumask {
    display: block;
  }
}
@media #{$smWidth} {
  .menumask {
    display: block;
  }
}
@media #{$xsWidth} {
  .menumask {
    display: block;
  }
}
