.pools_card {
  width: 100%;
}

.pools_card {
  padding: 40px 0 40px;
  &_content {
    padding: 30px 50px 40px;
    margin: 0 auto;
    width: 640px;
    height: 294px;
    background: #4b83eb;
    border-radius: 10px;
  }
  &_content_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      font-size: 16px;
      line-height: 16px;
      font-weight: bold;
      color: #4ec00a;
      &:last-child {
        font-size: 14px;
        line-height: 14px;
      }
    }
  }
  &_val {
    margin-top: 10px;
    font-size: 24px;
    color: #ffffff;
    line-height: 24px;
  }
  &_start {
    margin: 34px 0 12px;
    font-size: 20px;
    font-weight: 400;
    color: #ffffff;
    line-height: 24px;
    font-family: -apple-system, BlinkMacSystemFont, Roboto, "Segoe UI", Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  }
  &_progress__bar {
    position: relative;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    padding-top: 40px;
    p {
      width: 540px;
      height: 14px;
      background: #ffffff;
      border-radius: 7px;
      a {
        display: inline-block;
        height: 100%;
        background: #4ec00a;
        border-radius: 7px;
        vertical-align: super;
      }
    }
  }
  &_schedule {
    span {
      color: #fff;
      font-size: 14px;
      line-height: 16px;
      margin-top: 8px;
    }
  }
}
