.banner {
  width: 100%;
  margin: 0 auto;
  &_img {
    display: none;
    margin: 0 auto;
    width: 98%;
    height: 270px;
  }
  &_text {
    font-size: 44px;
    font-weight: 600;
    color: #22292f;
    line-height: 62px;
    margin-top: 50px;
  }
  &_text-title {
    display: none;
  }
  &_small_img {
    display: none;
  }
  &_big_img {
    width: 400px;
    height: 200px;
    border-radius: 10px;
    border: 1px solid #979797;
  }
  &_dec {
    padding: 0 20px;
    color: #fdfdfd;
    text-align: center;
    margin-top: 80px;
  }
  .first-intro {
    font-size: 28px;
    padding: 18px 0;
    font-weight: bold;

    .highlight {
      background-color: #4b83eb;
      background-image: linear-gradient(315deg, #ace65c, #4b83eb 74%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      filter: drop-shadow(0 3px 6px rgba(170,169,173,.9));
    }
  }
  .secord-intro {
    font-size: 14px;
    padding: 14px 0;
  }

  .liveOn {
    text-align: center;

    img {
      display: inline-block;
    }
  }

  .statistic {
    margin: 80px 60px 10px 60px;
    padding: 35px;
    box-sizing: border-box;
    position: relative;
    background: linear-gradient(315deg,#2d3436,#000 74%);
    border: 2px solid #2a2b2f;
    border-radius: 20px;
    box-shadow: 0 20px 50px rgba(0,0,0,.5);
    transition: .5s;
    overflow: hidden;
    background-image: url('../image/diamond.png');
    background-repeat: no-repeat;
    background-position: top right;
    filter: grayscale(100);

    .title {
      text-transform: uppercase;
      background-color: #4b83eb;
      background-image: linear-gradient(315deg, #ace65c, #4b83eb 74%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      filter: drop-shadow(0 3px 6px rgba(170,169,173,.9));
      font-weight: bold;
    }

    .statistic-container {
      padding: 14px 0;
    }

    .statistic-label {
      font-size: 14px;
      font-weight: 300;
      z-index: 1;
      transition: .5s;
      color: hsla(0,0%,100%,.5);
    }

    .statistic-value {
      font-size: 14px;
      font-weight: 300;
      z-index: 1;
      transition: .5s;
      color: #fdfdfd;
    }
  }
}
