.pools_banner {
  width: 100%;
}

.pools_banner {
  margin: 0 auto;
  &_text {
    background-color: #4b83eb;
    background-image: linear-gradient(315deg,#4b83eb,#ace65c 74%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: drop-shadow(0 3px 6px rgba(170,169,173,.9));
    padding: 80px 80px 40px 80px;

    @media (max-width: 768px) {
      padding: 8px 18px;
      margin: 0;

      p {
        padding: 0;
        margin: 0;
      }
    }
  }
  &_btn {
    display: inline-block;
    width: 128px;
    height: 48px;
    font-size: 16px;
    color: #22292f;
    font-weight: 600;
    line-height: 48px;
    border-radius: 24px;
    background: #4b83eb;
    &:hover {
      color: #fff;
    }
    &:last-child {
      margin-left: 24px;
      &:hover {
        color: #4b83eb;
      }
    }
  }
  &_btn_active {
    color: #fff;
    background: #4b83eb;
    box-shadow: 0px 4px 20px 0px rgba(15, 140, 32, 0.3);
  }

  &_btn_disable {
    color: #fff;
    background: #999;
    box-shadow: 0px 4px 20px 0px rgba(#999, 0.3);
  }
}
